import request from '../request'


// 获取列表
export function listSpuInfo(data) {
    return request({
        url: '/api/spuInfo/listSpuInfo',
        method: 'post',
        data
    })
}

// 新增
export function saveSpuInfo(data) {
    return request({
        url: '/api/spuInfo/saveSpuInfo',
        method: 'post',
        data
    })
}

// 修改
export function updateSpuInfo(data) {
    return request({
        url: '/api/spuInfo/updateSpuInfo',
        method: 'post',
        data
    })
}

// 删除
export function deleteSpuInfoById(params) {
    return request({
        url: '/api/spuInfo/deleteSpuInfoById',
        method: 'get',
        params: params
    });
}
