<template>
  <div id="style-operate">
    <!-- Form -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="1000" draggable>
      <el-form :model="form" :rules="rules" ref="ruleForm" label-width="120px">
        <el-row>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-upload class="avatar-uploader" action="/api/uploadFile/uploadFile" :data="fileData" :headers="headers" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
              <img v-if="form.urlStr" :src="form.urlStr" class="avatar"/>
              <el-icon v-else class="avatar-uploader-icon"><Plus/></el-icon>
            </el-upload>
          </el-col>

          <el-col :span="20"><div class="grid-content ep-bg-purple-light" />
            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('SpuView.spuCode')" :label-width="formLabelWidth" v-if="type != 'add'">
                  <el-input :readonly="true" v-model="form.spuCode" :placeholder="$t('SpuView.spuCodePlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('SpuView.spuName')" prop="spuName" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.spuName" :placeholder="$t('SpuView.spuNamePlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('SpuView.nameEn')" prop="nameEn" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.nameEn" :placeholder="$t('SpuView.nameEnPlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('SpuView.function')" prop="function" :label-width="formLabelWidth">
                  <el-select filterable v-model="form.function" :placeholder="$t('SpuView.functionPlaceholder')">
                    <el-option v-for="item in selectData.functionList" :key="item.id" :label="item.name"
                               :value="item.id"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('SpuView.series')" prop="series" :label-width="formLabelWidth">
                  <el-select filterable v-model="form.series" :placeholder="$t('SpuView.seriesPlaceholder')">
                    <el-option v-for="item in selectData.seriesList" :key="item.id" :label="item.name"
                               :value="item.id"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="24"><div class="grid-content ep-bg-purple-dark" />
                <el-form-item :label="$t('SpuView.specs')" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.specs" :placeholder="$t('SpuView.specsPlaceholder')" style="width: 100%" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24"><div class="grid-content ep-bg-purple-dark" />
                <el-form-item :label="$t('SpuView.remarks')" :label-width="formLabelWidth">
                  <el-input type="textarea" v-model="form.remarks" :placeholder="$t('SpuView.remarksPlaceholder')" :readonly="isReadonly" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24"><div class="grid-content ep-bg-purple-dark" />
                <el-form-item :label="$t('SpuView.remarksEn')" :label-width="formLabelWidth">
                  <el-input type="textarea" v-model="form.remarksEn" :placeholder="$t('SpuView.remarksEnPlaceholder')" :readonly="isReadonly" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" v-show="type != 'detail'" @click="submitButton()"> {{ $t('common.button.confirm') }} </el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {getFunctionPullDown, getSeriesPullDown} from "@/http/PullDownApi";
import {ElNotification} from "element-plus";
import {saveSpuInfo, updateSpuInfo} from "@/http/spuData/spuApi";

export default {
  name: "style-operate",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据
      rowData: {},
      // 行数据副本
      form: {},
      // 表单验证规则
      rules: {
        spuName: [
          {required: true, message: this.$t('SpuView.spuNamePlaceholder'), trigger: 'blur'},
        ],
        nameEn: [
          {required: true, message: this.$t('SpuView.nameEnPlaceholder'), trigger: 'blur'},
        ],
        function: [
          {required: true, message: this.$t('SpuView.functionPlaceholder'), trigger: 'change'},
        ],
        series: [
          {required: true, message: this.$t('SpuView.seriesPlaceholder'), trigger: 'change'},
        ],
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      formLabelWidth: '120px',

      selectData: {}, // 下拉的数据集合对象

      // 上传请求的请求头 存放token校验
      headers: {},
      // 请求接口需要携带的参数
      fileData: { folder: 'userPhotoUrl' },
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
      if (newVal == '') return
      if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
      if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
      if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    }
  },

  created() {
    // 后端如果验证 token 会报空 因为请求不是异步不会被路由守卫装配token 所以需要在发送请求时带上token
    this.headers = {token: localStorage.getItem("token")};
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getPullDownData();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getPullDownData() {
      getFunctionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.functionList = res.data.data;
      }).catch((erro) => { });
      getSeriesPullDown().then(res => {
        if (res.data.status == 200) this.selectData.seriesList = res.data.data;
      }).catch((erro) => { });
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible = false;
      this.isReadonly = false;
      this.form = {};
      this.rowData = {};
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    submitButton() {
      // 提交校验
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.type == "add") {
            saveSpuInfo(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          } else if (this.type == "edit") {
            updateSpuInfo(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          }
          this.closeButton();
        } else {
          return false;
        }
      });
    },


    // 上传之后的res结果集
    handleAvatarSuccess(res, file) {
      if (res.status == 200) {
        // vue.config.js 定义了后端请求前缀路径 可直接调用
        this.form.spuImg = res.data;
        this.form.urlStr = process.env.VUE_APP_BACKEND_API_URL + res.data;
      } else {
        this.$message.error(this.$t('common.message.networkError'));
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadError'), type: 'info',})
      }
      if (!isLt2M) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadSizeError'), type: 'info',})
      }
      return isJPG && isLt2M;
    }
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}

.el-select {
  width: 300px;
}

.el-input {
  width: 300px;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}

.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
</style>
